.modal {
  @include position($t: 50%, $l: 50%);

  padding: 40px;

  text-align: center;

  background-color: #ffffff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  transition: transform var(--transition) 200ms;

  padding: 40px;

  background-color: #ffffff;

  text-align: center;

  @media screen and (min-width: 320px) {
    transform: translate(-50%, -50%);

    max-width: 450px;
    width: calc(100% - 30px);

    .backdrop.is-hidden & {
      transform: translate(-50%, -50%) scale(0);
    }
  }

  @media screen and (min-width: 768px) {
    max-width: 450px;
    width: 100%;

    .backdrop.is-hidden & {
      transform: translate(-50%, -50%) scale(0);
    }
  }

  @media screen and (min-width: 1200px) {
    transform: translate(-50%, -50%) scale(1);

    max-width: 530px;
    width: 100%;

    .backdrop.is-hidden & {
      transform: translate(-50%, -50%) scale(0);
    }
  }
}

.modal__title {
  margin-bottom: 30px;

  font-family: 'Titan One';
  @include font(400, 26px, 1.1538, 0.04em);
  text-transform: uppercase;

  &--black {
    color: var(--title-color);
  }

  @media screen and (min-width: 320px) {
    font-size: 20px;
    line-height: 1.5;
  }

  @media screen and (min-width: 1200px) {
    font-size: 20px;
    line-height: 1.15;
  }
}

.modal__form {
  width: 100%;
}

.modal_form-field {
  position: relative;
}

.modal__input {
  width: 100%;
  padding: 9.5px 42px;
  margin-bottom: 28px;
  border: 1px solid rgba(33, 33, 33, 0.2);

  border-radius: 4px;
  outline: transparent;

  font-weight: 400;
  letter-spacing: 0.01em;

  transition: border-color var(--transition);

  &:focus {
    border-color: var(--accent-color);
  }

  &::placeholder {
    font-weight: 400;
    letter-spacing: 0.01em;

    color: rgba(33, 33, 33, 0.374);
  }

  @media screen and (min-width: 320px) {
    font-size: 12px;
    line-height: 1.1666;

    &::placeholder {
      font-size: 12px;
      line-height: 1.1666;
    }
  }

  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.1428;

    &::placeholder {
      font-size: 14px;
      line-height: 1.1428;
    }
  }
}

.modal__label {
  @include position($t: -17px);

  font-weight: 400;
  font-size: 12px;
  line-height: 1.1666;
  letter-spacing: 0.01em;

  color: #757575;

  transition: visibility var(--transition), opacity var(--transition);

  .modal__input:focus + &,
  .modal__textarea:focus + & {
    visibility: visible;
    opacity: 1;
  }

  @media screen and (min-width: 320px) {
    font-size: 12px;
    line-height: 1.1666;

    &::placeholder {
      font-size: 12px;
      line-height: 1.1666;
    }
  }

  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.1428;

    &::placeholder {
      font-size: 14px;
      line-height: 1.1428;
    }
  }
}

.modal__icon {
  @include position($t: 9px, $l: 12px);
  transition: fill var(--transition);

  fill: rgba(212, 20, 68, 0.3);

  .modal__input:focus ~ & {
    fill: var(--accent-color);
  }
}

.modal__textarea-container {
  position: relative;

  width: 100%;
}

.modal__textarea {
  width: 100%;
  height: 120px;
  padding: 12px 16px;
  margin-bottom: 20px;
  border: 1px solid rgba(33, 33, 33, 0.2);

  border-radius: 4px;

  resize: none;
  outline: transparent;

  &:focus {
    border-color: var(--accent-color);
  }

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.1428;
    letter-spacing: 0.01em;

    color: rgba(33, 33, 33, 0.374);
  }
}

.checkbox__label {
  @include flex-centred();

  font-weight: 400;

  letter-spacing: 0.03em;

  color: #757575;

  @media screen and (min-width: 320px) {
    flex-wrap: wrap;

    font-size: 12px;
    line-height: 1.1666;
  }

  @media screen and (min-width: 1200px) {
    flex-wrap: wrap;

    font-size: 14px;
    line-height: 1.7142;
  }
}

.modal__checkbox-container {
  margin-bottom: 30px;
}

.checkbox__icon {
  width: 16px;
  height: 15px;
  margin-right: 9px;
  border: 2px solid rgba(212, 20, 68, 0.2);

  border-radius: 2px;
  background-color: transparent;
  fill: transparent;

  .checkbox__input:checked + & {
    border: none;

    background-color: var(--accent-color);
    fill: #ffffff;
  }
}

.checkbox__link {
  margin-left: 3px;

  font-size: inherit;
  line-height: inherit;
  text-decoration: underline;

  color: var(--accent-color);
}

.modal__subscription {
  margin: 0 auto;
}

.subscription__button {
  margin: 0 auto;
  padding: 12px 35px;
  border-radius: 22px;
}

.modal-menu__button-icon {
  margin-left: 16px;

  fill: #ffffff;
}

.close-button {
  position: absolute;
  top: 8px;
  right: 8px;

  width: 30px;
  height: 30px;
  border: 1px solid rgba(212, 20, 68, 0.1);

  border-radius: 50%;

  background-color: transparent;

  transition: border var(--transition);

  &:hover,
  &:focus {
    border: 1px solid rgba(212, 20, 68, 0.2);
  }
}

.close-button__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  fill: rgba(212, 20, 68, 0.3);

  transition: fill var(--transition);

  .close-button:hover &,
  .close-button:focus & {
    fill: var(--accent-color);
  }
}
