*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body {
  color: var(--primary-text-color);
  margin: 0;
  font-family: DM Sans, sans-serif;
}

body.modal-open, body.modal-sub-open {
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

p, b, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}

button {
  cursor: pointer;
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  width: 100%;
  display: block;
}

address {
  font-style: normal;
}

:root {
  --accent-color: #d41443;
  --title-color: #d41443;
  --title-top-text-color: #ffa5ba;
  --primary-text-color: #000;
  --secondary-text-color: #907e82;
  --white-text-color: #fff;
  --purp-button-bg: #d41443;
  --white-button-bg: #fff;
  --white-button-accent-bg: #ecebeb;
  --purp-button-accent-bg: rgba(212, 20, 68, .7);
  --white-section-color: #fff;
  --purple-section-color: #fff5f6;
  --socials-bg-color: #fea5bb;
  --socials-icon-color: #fff;
  --mob-menu-bg: #e17992;
  --link-color: #907e82;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.button {
  transition: background-color var(--transition), box-shadow var(--transition), transform var(--transition);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  transform: translate(0)scale(1);
}

.button--white {
  color: var(--accent-color);
  background-color: var(--white-button-bg);
  transition: transform var(--transition);
}

.button--purp {
  color: var(--white-text-color);
  background-color: var(--purp-button-bg);
  transition: transform var(--transition);
}

.button--white:hover, .button--white:focus {
  background-color: var(--white-button-accent-bg);
  color: var(--accent-color);
  transform: translate(0)scale(1.11);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.button--purp:hover, .button--purp:focus {
  background-color: var(--purp-button-accent-bg);
  transform: translate(0)scale(1.11);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.mobile-menu {
  transform-origin: 100% 0;
  width: 250px;
  height: 100%;
  transition: right var(--transition) 100ms;
  background-color: #e17992;
  padding-top: 60px;
  position: absolute;
  top: 0;
  right: -35%;
  box-shadow: -8px 0 20px rgba(0, 0, 0, .25);
}

.mob-menu__backdrop.active .mobile-menu {
  right: 0;
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    width: 254px;
  }
}

.button-container {
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .button-container {
    padding: 0 32px;
  }
}

.mobile-menu__close-button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  position: absolute;
  top: 20px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .mobile-menu__close-button {
    right: 32px;
  }
}

.menu-close__icon {
  fill: #fff;
  transition: fill var(--transition);
}

.menu-close__icon:hover, .menu-close__icon:focus {
  fill: var(--accent-color);
}

.mobile-menu__button {
  width: 100%;
  border-radius: 22px;
  padding-top: 11px;
  padding-bottom: 11px;
}

.mobile-menu__button-icon {
  fill: var(--accent-color);
  margin-left: 16px;
}

.mobile-menu__list {
  margin-top: 20px;
}

.mobile-menu__item {
  padding: 12px 20px;
}

.mobile-menu__item:first-child {
  border-top: 1px solid #e18298;
}

.mobile-menu__item:last-child {
  border-bottom: 1px solid #e18298;
}

.mobile-menu__item + .mobile-menu__item {
  border-top: 1px solid #e18298;
}

.mobile-menu__link {
  color: var(--white-text-color);
  transition: color var(--transition);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.2857;
}

.mobile-menu__link:hover, .mobile-menu__link:focus {
  color: var(--accent-color);
}

.menu-open {
  overflow: hidden;
}

.modal {
  text-align: center;
  transition: transform var(--transition) 200ms;
  text-align: center;
  background-color: #fff;
  border-radius: 4px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 320px) {
  .modal {
    max-width: 450px;
    width: calc(100% - 30px);
    transform: translate(-50%, -50%);
  }

  .backdrop.is-hidden .modal {
    transform: translate(-50%, -50%)scale(0);
  }
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 450px;
    width: 100%;
  }

  .backdrop.is-hidden .modal {
    transform: translate(-50%, -50%)scale(0);
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    max-width: 530px;
    width: 100%;
    transform: translate(-50%, -50%)scale(1);
  }

  .backdrop.is-hidden .modal {
    transform: translate(-50%, -50%)scale(0);
  }
}

.modal__title {
  letter-spacing: .04em;
  text-transform: uppercase;
  margin-bottom: 30px;
  font-family: Titan One;
  font-size: 26px;
  font-weight: 400;
  line-height: 1.1538;
}

.modal__title--black {
  color: var(--title-color);
}

@media screen and (min-width: 320px) {
  .modal__title {
    font-size: 20px;
    line-height: 1.5;
  }
}

@media screen and (min-width: 1200px) {
  .modal__title {
    font-size: 20px;
    line-height: 1.15;
  }
}

.modal__form {
  width: 100%;
}

.modal_form-field {
  position: relative;
}

.modal__input {
  width: 100%;
  letter-spacing: .01em;
  transition: border-color var(--transition);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  margin-bottom: 28px;
  padding: 9.5px 42px;
  font-weight: 400;
}

.modal__input:focus {
  border-color: var(--accent-color);
}

.modal__input::-ms-input-placeholder {
  letter-spacing: .01em;
  color: rgba(33, 33, 33, .373);
  font-weight: 400;
}

.modal__input::placeholder {
  letter-spacing: .01em;
  color: rgba(33, 33, 33, .373);
  font-weight: 400;
}

@media screen and (min-width: 320px) {
  .modal__input {
    font-size: 12px;
    line-height: 1.1666;
  }

  .modal__input::-ms-input-placeholder {
    font-size: 12px;
    line-height: 1.1666;
  }

  .modal__input::placeholder {
    font-size: 12px;
    line-height: 1.1666;
  }
}

@media screen and (min-width: 1200px) {
  .modal__input {
    font-size: 14px;
    line-height: 1.1428;
  }

  .modal__input::-ms-input-placeholder {
    font-size: 14px;
    line-height: 1.1428;
  }

  .modal__input::placeholder {
    font-size: 14px;
    line-height: 1.1428;
  }
}

.modal__label {
  letter-spacing: .01em;
  color: #757575;
  transition: visibility var(--transition), opacity var(--transition);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.1666;
  position: absolute;
  top: -17px;
  left: 0;
}

.modal__input:focus + .modal__label, .modal__textarea:focus + .modal__label {
  visibility: visible;
  opacity: 1;
}

@media screen and (min-width: 320px) {
  .modal__label {
    font-size: 12px;
    line-height: 1.1666;
  }

  .modal__label::-ms-input-placeholder {
    font-size: 12px;
    line-height: 1.1666;
  }

  .modal__label::placeholder {
    font-size: 12px;
    line-height: 1.1666;
  }
}

@media screen and (min-width: 1200px) {
  .modal__label {
    font-size: 14px;
    line-height: 1.1428;
  }

  .modal__label::-ms-input-placeholder {
    font-size: 14px;
    line-height: 1.1428;
  }

  .modal__label::placeholder {
    font-size: 14px;
    line-height: 1.1428;
  }
}

.modal__icon {
  transition: fill var(--transition);
  fill: rgba(212, 20, 68, .3);
  position: absolute;
  top: 9px;
  left: 12px;
}

.modal__input:focus ~ .modal__icon {
  fill: var(--accent-color);
}

.modal__textarea-container {
  width: 100%;
  position: relative;
}

.modal__textarea {
  width: 100%;
  height: 120px;
  resize: none;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  margin-bottom: 20px;
  padding: 12px 16px;
}

.modal__textarea:focus {
  border-color: var(--accent-color);
}

.modal__textarea::-ms-input-placeholder {
  letter-spacing: .01em;
  color: rgba(33, 33, 33, .373);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.1428;
}

.modal__textarea::placeholder {
  letter-spacing: .01em;
  color: rgba(33, 33, 33, .373);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.1428;
}

.checkbox__label {
  letter-spacing: .03em;
  color: #757575;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  display: flex;
}

@media screen and (min-width: 320px) {
  .checkbox__label {
    flex-wrap: wrap;
