  fill: rgba(0, 0, 0, 0);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(212, 20, 68, .2);
  border-radius: 2px;
  margin-right: 9px;
}

.checkbox__input:checked + .checkbox__icon {
