}

.backdrop {
  z-index: 5;
  width: 100%;
  height: 100%;
  transition: visibility var(--transition), opacity var(--transition);
  background: url("backdrop30.7b5c01e3.png"), rgba(255, 255, 255, .7);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.mob-menu__backdrop {
  z-index: 5;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  width: 100%;
  height: 100%;
  transition: visibility var(--transition), opacity var(--transition);
  background: url("backdrop30.7b5c01e3.png"), rgba(255, 255, 255, .7);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.mob-menu__backdrop.active {
  z-index: 5;
  visibility: visible;
  opacity: 1;
  pointer-events: all;
  width: 100%;
  height: 100%;
  transition: visibility var(--transition), opacity var(--transition);
  background: url("backdrop30.7b5c01e3.png"), rgba(255, 255, 255, .7);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

