  min-width: 230px;
  min-height: 400px;
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  transition: transform .55s ease-in-out, scale .55s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
